/* eslint-disable jsx-a11y/alt-text */
import { React, useRef, useLayoutEffect } from "react";
import '../css/style.scss';
import gym from '../img/gym-1.jpg';
import TimeTable from "./TimeTable";
import MapComp from "./MapComp";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const Main = () => {

    const card1AnimRef = useRef(null);
    const card2AnimRef = useRef(null);
    const card3AnimRef = useRef(null);


    useLayoutEffect(() => {
        const tl = gsap.timeline({ defaults: { scale: (.8, .8) } });

        ScrollTrigger.matchMedia({
            "(max-width:910px)": function () {
                tl.from(card2AnimRef.current, {
                    opacity: 0, duration: 1,
                    scrollTrigger: {
                        trigger: card2AnimRef.current,
                        start: "center center",
                        end: "center center",
                        scrub: 1
                    }
                }).from(card3AnimRef.current, {
                    opacity: 0, duration: 1,
                    scrollTrigger: {
                        trigger: card2AnimRef.current,
                        start: "center center",
                        end: "center center",
                        scrub: 1
                    }

                })
            },
            "(min-width:910px)": function () {

                tl.from(card2AnimRef.current, { opacity: 0, duration: 1.2 })
                    .from(card1AnimRef.current, { opacity: 0, duration: 1.2 })
                    .from(card3AnimRef.current, { opacity: 0, duration: 1.2 }, "-=1")
            }
        });
    });


    return (
        <div>

            <main>
                <div className="main-container">
                    <div className="main-img">
                        <img src={gym} alt="" srcSet="" />

                        <div className="txt-holder">
                            <div className="main-txt">
                                <h1>EHRN RTBE MERKEZNDE YERNZ ALIN</h1>
                                <p>Biz sk alyoruz ve tm ar ileri siz yapyorsunuz. Hep istediin vcudu edinmeye bugnden bala!</p>
                            </div>
                        </div>

                    </div>

                    <div className="main-card">
                        <div ref={card1AnimRef} className="card-1">
                            <div className="circle"><i><img src="data:image/svg+xml;base64," width="64" height="64" /></i></div>
                            <div className="card-content">
                                <h3>EKPMANLAR</h3>
                                <p>En yeni ekipmanlarmz sizi yeni vcudunuza gtrecek ekipmanlardr.</p>
                            </div>
                        </div>
                        <div ref={card2AnimRef} className="card-2">
                            <div className="circle"><i><img src="data:image/svg+xml;base64," width="64" height="64" /></i></div>
                            <div className="card-content">
                                <h3>ETMENLER</h3>
                                <p>Gen ve dinamik ekibimiz sizi en iyi motivasyonda tutar.</p>
                            </div>
                        </div>
                        <div ref={card3AnimRef} className="card-3">
                            <div className="circle"><i><img src="data:image/svg+xml;base64," width="64" height="64" /></i></div>
                            <div className="card-content">
                                <h3>PROGRAM</h3>
                                <p>Her seviyeye uygun kiiselletirilmi profesyonel alma programlar.</p>
                            </div>
                        </div>
                    </div>

                </div>

                <div className="main-end">
                    <div className="end-holder">
                        <p>Biz sadece sizi  <u>cesaretlendirmek</u>, <u>glendirmek</u> ve beraber <u>elenmek</u> istiyoruz.</p>
                    </div>
                </div>
            </main>
            <TimeTable />
            <MapComp />
        </div>

    );
}

export default Main;