import { React } from "react";
import "../../css/dstyle.scss";

const UpdateAbout = () => {

    return (
        <div className="about-area">
            <div className="container">
                <div className="about-head">
                    <h3>HAKKIMIZDA GNCELLEME</h3>
                </div>
                <div className="file-container">
                    <div className="file-form">
                        <div className="file-head">
                            <h3>Video Ykleme</h3>
                            <hr />
                        </div>
                        <form className="f-form" action="" method="" encType="multipart/form-data">

                            <p className="card-text"><input type="file" name="dosya" /></p>

                            <input type="submit" value="YKLE" name="buton" className="file-btn" />

                        </form>
                        <div className="file-warning">
                            * zin verilen dosya tr: mp4,mpeg <br />
                            * zin verilen en yksek boyut: ... Mb
                        </div>
                    </div>
                </div>
                <div className="file-container">
                    <div className="file-form">

                        <div className="file-head">
                            <h3>erik Ykleme</h3>
                            <hr />
                        </div>
                        <form action="" className="guncelle-form" method="">
                            <div className="f-icerik">
                                <textarea style={{ "resize": "none" }} name="icerik" id="" cols="30" rows="15"></textarea>
                                <div className="guncelle">
                                    <input type="submit" name="buton" value="GNCELLE" className="guncelle-btn" />
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default UpdateAbout;